* {
  margin: 0;
  padding: 0;
}

h2 {
  font-family: Poppins, "Roboto", sans-serif;
  letter-spacing: 1px;
  font-weight: 500;
  color: #333;
  margin-top: 55px;
  text-align: center; /* Center heading for all screens */
}

.form {
  width: 90%; /* Use a percentage for dynamic sizing */
  max-width: 827px; /* Limit the width on larger screens */
  background-color: #fff;
  padding: 20px 30px;
  border-radius: 8px;
  margin: 30px auto; /* Centered horizontally on all screens */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.form p {
  color: #777;
  padding-bottom: 8px;
}

.form-group {
  display: flex;
  flex-direction: column; /* Stack elements vertically on small screens */
  align-items: flex-start;
  margin-top: 15px;
}

.form-group label {
  width: 100%; /* Full width on small screens */
  font-size: 15px;
  color: #555;
  margin-bottom: 5px; /* Add spacing between label and input */
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="tel"],
.form-group input[type="file"],
.form-group textarea {
  width: 100%; /* Full width for inputs */
  padding: 10px;
  border: 1px solid #caccd0;
  background-color: #fafbfc;
  border-radius: 4px;
  font-size: 13px;
  box-sizing: border-box;
  color: #555;
}

.form-group textarea {
  height: 80px;
  resize: vertical;
}

.form-group input[type="file"] {
  padding: 5px;
  font-size: 12.5px;
  color: #777;
}

.star {
  color: red;
}

.description-message, 
.input-description-message {
  font-size: 13px;
  color: #777;
  margin: 10px 0; /* Adjust spacing for all screens */
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  font-size: 12.5px;
  color: #777;
  letter-spacing: 0.5px;
}

button[type="submit"] {
  width: 100%; /* Full width on small screens */
  max-width: 200px; /* Limit width on larger screens */
  background-color: #4CAF50;
  color: white;
  padding: 12px;
  margin: 15px auto; /* Center button */
  border: none;
  border-radius: 18px;
  cursor: pointer;
  font-size: 16px;
  display: block; /* Ensure centering works */
}

button[type="submit"]:hover {
  background-color: #45a049;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  background-color: #f5fbff;
  border-color: #6fb4ea;
}

#captcha {
  text-align: center; /* Center captcha content */
  margin-top: 15px;
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
  h2 {
    font-size: 20px; /* Adjust heading size */
    margin-top: 40px;
  }

  .form {
    padding: 15px; /* Reduce padding for smaller screens */
  }

  button[type="submit"] {
    font-size: 14px; /* Adjust button font size */
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 18px; /* Further reduce heading size */
    margin-top: 30px;
  }

  .form {
    padding: 10px; /* Reduce padding further */
  }

  button[type="submit"] {
    font-size: 14px;
    padding: 10px; /* Adjust button padding */
  }

  .description-message,
  .input-description-message {
    font-size: 12px; /* Smaller font for very small screens */
  }

  .form-group label {
    font-size: 14px; /* Adjust label size */
  }

  .form-group input[type="text"],
  .form-group input[type="email"],
  .form-group input[type="tel"],
  .form-group input[type="file"],
  .form-group textarea {
    font-size: 12px; /* Adjust input font size */
  }
}

